.body {
  margin: 0;
  padding: 0;
}
.containerAbout {
  /* background-image: url(../component/stephanus.jpg); */
  background-image: url(../component/home.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
  display: flex;
  align-items: center;
  padding: 0 5%;
}
.about {
  max-width: 600px;
}
.about h1 {
  position: relative;
  font-size: 56px;
  line-height: 1.2;
  margin-right: 250px;
  font-weight: 700;
}
/* .about h1::before{
 
} */
.about h3 {
  margin-right: 360px;
  font-size: 32px;
  font-weight: 700;
  color: crimson;
}
.about p {
  font-size: 16px;
  margin: 20px;
  font-weight: 450;
  color: whitesmoke;
}
.about .btn-box {
  display: flex;
  justify-content: space-between;
  width: 345px;
  height: 50px;
}
.btn-box a {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 100%;
  background: crimson;
  border: 2px solid crimson;
  border-radius: 8px;
  font-size: 19px;
  color: black;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 1px;
  z-index: 1;
  overflow: hidden;
  transition: 0.5s;
}
.btn-box a:hover {
  color: crimson;
}
.btn-box a:nth-child(2) {
  background: transparent;
  color: crimson;
}
.btn-box a:nth-child(2):hover {
  color: black;
}
.btn-box a:nth-child(2)::before {
  background: crimson;
}
.btn-box a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  transition: 0.5s;
  background: rgb(15, 6, 6);
  z-index: -1;
}
.btn-box a:hover:before {
  width: 100%;
}
.containerAbout-imgHover{
  position: absolute;
  top: 15%;
  right: 30px;
  width: 545px;
  height: 100%;
  background: transparent;
  transition: 3s;
}
.containerAbout-imgHover:hover{
  background: #081b29;
  opacity: .8;
}
