.body{
    margin: 0;
    padding: 0;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.container{
    /* width: 100%;
    height: 100vh; */
    /* justify-content: center;
    align-items: center; */
    display: flex;
    /* background-color: aqua; */
}
.wraper{
    background-color: rgb(0, 0, 0);
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 15px;
    padding: 15px;
    width: 100%;
    height: 80px;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0,5);
    border-bottom: solid 2px crimson;

}
.prof{
    display: flex;
    flex-direction: row;
    font-size: 1.5em;
    padding: 15px;
}
.first{
    color: white;
    font-weight: 700;
}
.two{
    color: crimson;
    font-weight: 700;
    padding-left: 4px;
}
.navigition{
    display: flex;
    justify-content: center;
    align-items: center;
}
.navigition li{
    list-style: none;
    padding: 15px;
}
.navigition li a {
    color: white;
    font-size: 20px;
    text-decoration: none;
    text-transform: capitalize;
}
.navigition #active{
    background-color: crimson;
    color: white;
    padding: 15px;
    border-radius: 15px;
}
.navigition li a:hover{
    color: crimson;
    transition: all .0,3s ease-in-out;
}
.navigition li img{
    width: 70px;
    height: 70px;
    border-radius: 70px;
}
.navigition li img:hover{
    width: 85px;
    height: 85px;
    border-radius: 85px;
}
